import cdstyle from "./style.css";
//import { LazyLoadImage } from 'react-lazy-load-image-component';
import support from "../../assets/service/support.svg";
import target from "../../assets/service/target.svg";
import preparetion from "../../assets/service/preparetion.svg";
import comunication from "../../assets/service/comunication.svg";

const CDService = () => {
  return (
    <div class={cdstyle.serviceContent} id="What">
      <div class={cdstyle.serviceContentContainer}>
        <div class={cdstyle.serviceContentLeft}>
          <h2>
            Canada District - your <span>trusted</span> immigration partner!
          </h2>
          <p>
            Canada District offers personalized guidance and support throughout
            the immigration process, expert advice on the best program for your
            unique situation, and assistance with completing your paperwork to
            maximize your chances of success.
          </p>
        </div>
        <div class={cdstyle.serviceContentRight}>
          <p>Our services include:</p>
          <div class={cdstyle.serviceContentBox}>
            <div class={cdstyle.serviceContentBoxItem}>
              <img
                src={support} // use normal <img> attributes as props
                alt="support"
                width={91}
                height={91}
              />
              <h3>Free initial consultation</h3>
            </div>
            <div class={cdstyle.serviceContentBoxItem}>
              <img
                src={target} // use normal <img> attributes as props
                alt="target"
                width={91}
                height={91}
              />
              <h3>Immigration program selection</h3>
            </div>
            <div class={cdstyle.serviceContentBoxItem}>
              <img
                src={preparetion} // use normal <img> attributes as props
                alt="preparetion"
                width={91}
                height={91}
              />

              <h3>Preparation</h3>
            </div>
            <div class={cdstyle.serviceContentBoxItem}>
              <img
                src={comunication} // use normal <img> attributes as props
                alt="comunication"
                width={91}
                height={91}
              />
              <h3>Communication with the Canadian government</h3>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default CDService;
